<template>
  <div class="root">
    <div v-for="item in displayText">
      <DisplayQA :Question=item.Question :Answer=item.Answer />
    </div>
  </div>
</template>

<script>
import DisplayQA from "../../components/DisplayQA";
export default {
  name: "Written",
  data(){
    return {
      displayText:[
        {
          Question: 'Q: 博乐科技的校招笔试将如何安排？',
          Answer:   'A: 本次校招将采用"短频多次"的方式进行笔试安排，减少各个环节的等待时间。切记，早笔试，早面试哦~'
        },
        {
          Question: 'Q: 不小心错过了笔试怎么办？',
          Answer:   'A: 笔试是面试环节前必须完成的，早笔试，早面试，完成笔试后才有机会进入到面试环节。如果因各类原因错过笔试，可以顺延参加下一批次，届时会通过邮件、短信等进行通知。'
        }
      ]
    }
  },
  components:{
    DisplayQA
  }
}
</script>

<style scoped>

</style>



